import React from "react";
import { graphql } from "gatsby";
import Layout from "../components/layout";
import { Helmet } from 'react-helmet'
import { useLocation } from '@reach/router';
import Hero from "../components/block_types/hero.js";
import About from "../components/block_types/about.js";
import Services from "../components/block_types/services.js";
import Blog from "../components/block_types/blog.js";
import Newsletter from "../components/block_types/newsletter.js";
import Marque from "../components/block_types/marque.js";
import Contact from "../components/block_types/contact.js";
import InsideHero from "../components/block_types/inside_hero.js";
import Portfolio from "../components/block_types/portfolio.js";
import ContentDetail from "../components/block_types/content_detail.js";
import TagBlogs from "../components/block_types/tag_blogs";
import CategoryBlogs from "../components/block_types/category_blogs";

export const query = graphql `

{

allMarkdownRemark(filter: {frontmatter: {api_handle: {eq: "category"}}}) {
    nodes {
      frontmatter {
        api_handle
        category
        date_created
        image
        meta_description
        meta_keywords
        meta_title
        og_description
        og_image
        og_site_name
        og_title
        page_type
        seo_title
        short_description
        title
        twitter_card_type
        twitter_site
        block_types {
          type
          attrs {
            values {
              back_to_link
              back_to_link_text
              button_link
              button_link_1
              button_text
              button_text_1
              description
              image
              images
              details
              link
              num_items
              short_description
              terms
              title
              title_1
              title_2
              title_3
              title_4
              sub_title
              type
              video_path
              accordian_items {
                description
                title
              }
              content
              marque_1 {
                title
              }
              marque_2 {
                title
              }
            }
          }
        }
        portfolio_items {
          image
          title
          title_2
        }
      }
    
    }
  }
}

`

/*export const Head = () => <Seo data={data} />;*/



export default function Category({ data }) {
  
  const page = data.allMarkdownRemark.nodes[0].frontmatter;
  const blocks = page.block_types;
  const location = useLocation();
  const domainUrl = location.origin;
  const CurrentPageUrl = location.href;

  return (
    <Layout>
     
      <Helmet>
            <title>{page.seo_title}</title>
            <meta content={page.meta_keywords} name="keywords" />
            <meta content={page.meta_description} name="description" />
            <meta property="og:title" content={page.og_title} />
            <meta property="og:description" content={page.og_description} />
            <meta property="og:image" content={`${domainUrl}/assets/${page.og_image}`} />
            <meta property="og:site_name" content={page.site_name} />
            <meta property="og:url" content={CurrentPageUrl} />
            <meta name="twitter:card" content={page.twitter_card_type} />
            <meta name="twitter:title" content={page.seo_title} />
            <meta name="twitter:description" content={page.og_description} />
            <meta name="twitter:image" content={`${domainUrl}/assets/${page.og_image}`} />
            <meta name="twitter:site" content={page.twitter_site} />
      </Helmet>

     
      {blocks &&
        blocks.map((item, index) => {
         
          
          if (item.attrs && item.attrs.values) {
            switch (item.attrs.values.type) {
              case 'hero':
                return <Hero key={index} data={item.attrs.values} />;
              case 'about':
                return <About key={index} data={item.attrs.values} />;
              case 'services':
                return <Services key={index} passedData={item.attrs.values} />;
              case 'portfolio':
                return <Portfolio key={index} passedData={item.attrs.values} />;
              case 'blog':
                return <Blog key={index} passedData={item.attrs.values} />;
              case 'blog_detail':
                return <ContentDetail key={index} passedData={item.attrs.values} />;
              case 'newsletter_form':
                return <Newsletter key={index} data={item.attrs.values} />;
              case 'marque':
                return <Marque key={index} data={item.attrs.values} />;
              case 'contact':
                return <Contact key={index} data={item.attrs.values} />;
              case 'inside_hero':
                return <InsideHero key={index} data={item.attrs.values} />;
              case 'tag_blogs':
                return <TagBlogs key={index} passedData={item.attrs.values} />;
              case 'category_blogs':
                return <CategoryBlogs key={index} passedData={item.attrs.values} />;
              default:
                return null;
            }
          } else {
            return null;
          }
        })}
    </Layout>
  )}

